import React, { Component } from 'react'
import './main'
import './WhyUs.css'


export default class WhyUs extends Component {
  render() {
    return (
      <>
        <section class="counters">
			<div class="container">
				<div>
					<div class="counter" data-target="60000">250+</div>
					<h3>Clients</h3>
				</div>
				<div>
					<div class="counter" data-target="15000">160+</div>
					<h3>Employees</h3>
				</div>
				<div>
					<div class="counter" data-target="9000">280+</div>
					<h3>Projects</h3>
				</div>
			</div>
		</section>
		<script src="main.js"></script>
      </>
    )
  }
}
